
import { useState } from "react";
import { Button, Col, Container, Form, Row } from "react-bootstrap";
import dict from "./Jejudo.json";
import Example from "./Modal"

function Dict() {
    const [search, setSearch] = useState("");
    const [searchList, setSearchList] = useState([]);

    const onHandleSelect = (e) => {
        console.log(e.target.value);
    }

    const onTypeSearch = (e) => {
        setSearch(e.target.value);
    }

    const onClickSearch = () => {
        if (search.length === 0)
            setSearchList([]);
        else {
            setSearchList(dict.filter(item => {
                return item.accent_word.includes(search);
            }))
        }
    }

    return (
        <Container >
            <Row style={{ margin: "10px 0px" }}>
                <Col sm={6}>
                    <h1>사투리 사전</h1>
                </Col>
                <Col sm={6}>
                    <Example />
                </Col>
            </Row>
            <Row style={{ margin: "10px 0px" }}>
                <Col sm={6}>
                    <Form.Select id="region" aria-label="지역 선택" onChange={onHandleSelect}>
                        <option value="0">지역을 선택해주세요!</option>
                        <option value="1">여기</option>
                        <option value="2">저기</option>
                        <option value="3">거기</option>
                    </Form.Select>
                </Col>
                <Col sm={6}>
                    <Form
                        className="d-flex"
                        onChange={onTypeSearch}
                        onSubmit={(e) => { e.preventDefault(); onClickSearch(); }}>
                        <Form.Control
                            type="search"
                            placeholder="Search"
                            className="me-2"
                            aria-label="Search"
                        />
                        <Button variant="outline-success" onClick={onClickSearch}>Search</Button>
                    </Form>
                </Col>
            </Row>
            {
                searchList.map(item => {
                    return (
                        <div key={item.id}>
                            <Row>
                                <Col sm={1} />
                                <Col sm={3} className='box fw-bold'>{item.accent_word}</Col>
                                <Col sm={7} className='box'>{item.definition}</Col>
                                <Col sm={1} />
                            </Row>
                        </div>
                    );
                })
            }
        </Container >
    )
}

export default Dict;